/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  min-height: 100%;
  font-family: "Roboto", sans-serif; }
  html * {
    margin: 0;
    padding: 0; }

*, *:before, *:after {
  box-sizing: inherit;
  outline: none; }

body {
  font-size: 1.6rem;
  height: 100vh;
  background-color: #fafafa; }

