/** Media Queries **/
.element-box {
  border: 0.1rem, solid #dee1e8;
  padding: 2.4rem;
  position: relative;
  margin-bottom: 2.4rem; }
  .element-box:last-child {
    margin-bottom: 0rem; }
  .element-box .element-box {
    border: 0;
    padding: 0rem; }
  .element-box--collapsed .element-box {
    height: 0;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
    overflow: hidden;
    border: 0;
    padding: 0; }
  .element-box--collapsed h3 {
    margin-bottom: 0 !important; }
    .element-box--collapsed h3 img {
      transform: rotate(0deg) !important; }
  .element-box .tag {
    background-color: #DB171D;
    color: white;
    padding: .8rem 1.2rem;
    right: 0;
    top: 0;
    position: absolute; }
  .element-box h3 {
    margin-bottom: 2.4rem;
    cursor: pointer;
    display: inline-block; }
    .element-box h3 img {
      margin-right: 2.4rem;
      transform: rotate(180deg); }
    .element-box h3 + span {
      margin-left: 1.6rem;
      font-family: Roboto;
      font-size: 1.2rem;
      line-height: 1.4rem;
      color: #105AA3;
      letter-spacing: initial; }
  .element-box .element-box__content {
    margin-bottom: 1.6rem;
    border: 0.1rem, solid #dee1e8;
    padding: 0.8rem; }
    .element-box .element-box__content:last-child {
      margin-bottom: 0rem; }
    .element-box .element-box__content .info {
      display: flex;
      flex-direction: column;
      margin-bottom: 2.4rem; }
      @media (min-width: 768px) {
        .element-box .element-box__content .info {
          flex-direction: row; } }
      .element-box .element-box__content .info--collapsed {
        margin-bottom: 0rem; }
        .element-box .element-box__content .info--collapsed + .info-condition__content {
          height: 0;
